                                            <br><br>
                                            <strong></strong>
                                        </v-card-text>
                                    </v-card>
                                </v-flex>
                                <v-flex xs12 md4>
                                    <v-card class="elevation-0 transparent">
                                        <v-card-text class="text-xs-center">
                                            <v-icon x-large class="blue--text text--lighten-2">build</v-icon>
                                        </v-card-text>
                                        <v-card-title primary-title class="layout justify-center">
                                            <div class="headline text-xs-center"></div>
                                        </v-card-title>
                                        <v-card-text>
                                            <br>
                                            <br>
                                            <strong></strong><br>
                                            <strong></strong>
                                        </v-card-text>
                                    </v-card>
                                </v-flex>
                            </v-layout>
                        </v-container>
                    </v-flex>
                </v-layout>
            </section>

            <section>
                <v-parallax :src="src.main2" height="380">
                    <v-layout column align-center justify-center>
                        <div class="headline white--text mb-3 text-xs-center"></div>
                        <em></em>
                        <v-btn class="blue lighten-2 mt-5"
                               dark
                               large
