                            <v-icon class="red--text">favorite</v-icon>
                            by <a class="white--text" href="/" target="_blank">xuxuzhaozhao</a>
                        </div>
                    </v-flex>
                </v-layout>
            </v-footer>
        </v-content>

        <!--  -->
        <x-menu :menuShow="menuShow"
                @menuHide="menuHide"
                :isMenu="isMenu">
        </x-menu>

    </v-app>
</template>

<script>
import XMenu from "./menu";
export default {
  components: {
    XMenu
  },
  data() {
    return {
      title: "TYTF",
      menuShow: false,
      isMenu: true,
      src: {}
    };
  },
  created() {
    document.getElementsByTagName('html')[0].style.overflowY = 'scroll';
    document.title = "";
    this.mainImage();
  },
  methods: {
    menuMenu() {
      this.isMenu = true;
      this.menuShow = true;
    },
    menuPlay() {
      this.isMenu = false;
      this.menuShow = true;
    },
    goTYTF() {
      window.open("https://www.amap.com/place/B0FFGXMG72");
    },
    menuHide() {
      this.menuShow = false;
    },
    mainImage() {
      this.$http.get(`${this.$domain}/api/TYTF/head`).then(res => {
        console.log(res.data.data)
        this.src = res.data.data;
      });
    }
  }
};
</script>

<style scoped>
/* html {
  overflow-y: scroll;
} */
</style>



